/*Modal*/
  .fade {
    opacity: 0;
    -webkit-transition: opacity 0.15s linear;
    transition: opacity 0.15s linear;
  }
  .fade.in {
    opacity: 1;
  }

  .modal-open {
    overflow: hidden;
  }
  .modal {
    text-align: center;
    display: none;
    overflow: hidden;
    position: fixed;
    top: 5vh;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    -webkit-overflow-scrolling: touch;
    outline: 0;

  }
  .modal.fade .modal-dialog {
    -webkit-transform: translate(0, -25%);
    transform: translate(0, -25%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  }
  .modal.in .modal-dialog {
    -webkit-transform: translate(0, 0);
    transform: translate(10, 0);
  }
  .modal-open .modal {
    overflow-x: hidden;
    overflow-y: auto;
  }
  .modal-dialog {
    position: relative;
    width: auto;
    margin: 10px;
  }
  .modal-content {
    position: relative;
    background-color:white;
    border: 4px solid white;
    box-shadow: 0 3px 9px rgb(0, 0, 0);
    -webkit-background-clip: padding-box;
            background-clip: padding-box;
    outline: 0;
  }
  .modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background-color: #000000;
  }
  .modal-backdrop.fade {
    opacity: 0;
    filter: alpha(opacity=0);
  }
  .modal-backdrop.in {
    opacity: 0.5;
    filter: alpha(opacity=50);
  }
  .modal-header {
    padding: 15px;
    
  }
  .modal-header .close {
    margin-top: -2px;
  }
  .Footer-Message{
    font: 20px  Bradley Hand ITC, sans-serif;
    font-weight: bold;
  }
  .Footer-Btn{
    color: #A66FA6;
    font: 20px  Bradley Hand ITC, sans-serif;
    font-weight: bold;
    text-decoration: underline;
  }
  .Footer-Btn:hover{
    cursor: pointer;
  }
.Modal-Title{
    font: 38px  Bradley Hand ITC, sans-serif;
    color: #A66FA6;
}
.Modal-Close-Btn{
    padding: 10px;
