 }
.Modal-Close-Btn:focus{
   outline: none;
}
  .modal-body {
    
    position: relative;
    padding: 15px;
  }
  .modal-footer {
    padding: 15px;
    text-align: center;
    border-top: 1px solid #A66FA6 ;
