/*Messenger*/
.MessengerContainer {
    border: rgba(0, 0, 0, 0.603) solid;
    margin: 0 auto;
    height: 100%;
    width: 80%;
    display: grid;
    grid-template-columns: 30% auto;
    min-height: 90vh;
    max-height: 90vh;
  }
  .sidepanel {
    position: relative;
    overflow-y: auto;
    border-right: solid rgba(0, 0, 0, 0.603);
    grid-column: 1;
    max-height: 90vh;
  
  }
  .mainpanel{
    position: relative;
    grid-column: 2;
    display: grid;
    grid-template-rows: 15% 75% auto;
    max-height: 89.5vh;
  }
  
  .chatpanel {
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column-reverse;
